body {
  background: #f5f7f8;
}

.sidebar {
  background: #20272a;
  width: 175px;
  position: fixed;
  height: 100%;
  z-index: 10000;
  transition: all .5s;
  top: 0;
  left: 0;
}
.sidebar .navicon {
  text-align: right;
  padding: 10px 15px 15px 10px;
  color: #ffffff;
}
.sidebar .navicon a {
  color: #ffffff;
  font-size: 22px;
  display: inline-block;
}
.sidebar .sidebar-menu li a {
  display: block;
  padding: 10px;
  background: transparent;
  color: #ffffff;
  font-size: 16px;
  transition: all .5s linear;
}
.sidebar .sidebar-menu li a:hover {
  background: #03c4e8;
  text-decoration: none;
}
.sidebar .sidebar-menu li a i {
  margin-right: 10px;
  font-size: 15px;
}
.sidebar #menuicon {
  transition: all 0.5s;
}

body.sidebar-mini .sidebar {
  width: 50px;
}
body.sidebar-mini .sidebar .sidebar-menu li a {
  text-align: center;
}
body.sidebar-mini .sidebar .sidebar-menu li a span {
  display: none;
  transition: all .5s linear;
}

.page-content {
  width: 600px;
  margin-left: 75px;
  transition: all 0.5s;
  display: inline-block;
}

#markets {
  display: grid;
  width: 400px;
  border: 1px gray;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  grid-auto-rows:minmax(150px, auto);
}

#markets > div {
  border: 1px solid #E0E0E0;
  padding: 1em;
  width: 100%;
}

.market-title {
  font-size: 1.5em;
  font-weight: bold;
  margin-top: 0px;
}

.orderbook {
  margin-top: 15px;
  width: 95%;
}

.orderbook-header-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-gap: 2px;
}

.orderbook-header {
  font-weight: bold;
  border: 0px solid black;
  background: #b1d1de;
  padding-left: 4px;
}

.orderbook > div {
  padding-left: .2em;
}

.menu-icon {
  background-color: transparent;
  border: none;
  outline: none;
}

.market-orderbook-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-gap: 2px;
  margin-top: 2px;
}

.market-orderbook-row > div {
  padding-left: 4px;
  background: rgb(232, 232, 232);
  margin-top: 1px;
}

#bet-slip, #bet-slip-login {
  /* display: none; */
  float: right;
  width: 300px;
  min-height: 400px;
  /* height: 600px; */
  margin: 95px 10px 10px 10px;
  border: 1px solid black;
  padding: 0px 0px 10px 0px;
  top: 0;
  right: 0;
  position: fixed;
  background: rgb(242, 242, 242);
}

#bet-slip-login {
  display: none;
}

#bet-slip-title, #close-bet-slip {
  display: inline-block;
}

#bet-slip > * {
  margin: 10px 15px 0px 10px;
}

#bet-slip-login > * {
  margin: 10px 15px 0px 10px;
}

#close-bet-slip {
  float: right;
  background: none;
  border: none;
}

.market-orderbook-row:hover > div {
  text-decoration: underline;
  cursor: pointer;
}

#bs-bid-select, #bs-ask-select {
  display: inline-block;
  border: 1px solid rgb(170, 168, 168);
  padding: 2px 5px;
  margin-left: -1px;
  width: 50px;
  text-align: center;
  color: rgb(170, 168, 168);
}

#bs-bid-select {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

#bs-ask-select {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

#bs-bid-select:hover, #bs-ask-select:hover {
  cursor: pointer;
}

#american-price {
  margin-left: 5px;
}

#decimal-price {
  margin-left: 13px;
}

#bs-mbtc-amount {
  margin-left: 24px;
}

#bs-usd-amount {
  margin-left: 35px;
}

#orderbook > * {
  margin-top: .25em;
  margin-bottom: .25em;
}

#orderbook-table {
  width: 100%;
  table-layout: fixed;
}

#orderbook-table td {
  border: 1px solid #999;
  /* width: 100%; */
  padding-left: 2px;
}

#orderbook-table thead {
  font-weight: bold;
  /* border: 1px solid #999; */
}

#bs-submit {
  margin-top: 1em;
}

#bs-submit-button {
  /* margin-top: 10px; */
  border-radius: 5px;
  background-color: #b1d1de;
  border: 1px solid black;
  width: 65px;
  font-weight: bold;
}

#bs-submit-button:focus {outline:0;}

#bs-submit-button:active {background-color: #b1d10e;}

#user-banner {
  float: right;
  margin-right: 20px;
  margin-top: 5px;
}

#bs-bet-succeed {
  display: none;
  color: green;
  font-weight: bold;
}

#bs-bet-failed {
  display: none;
  color: red;
  font-weight: bold;
}